<template>
  <div id="form">
    <div class="form_search">
      <div 
        v-if="placeByName"
        class="byName"
      >
        <input type="text" placeholder="search..." v-model="query" @keyup.enter="sendQuery"/>
        <button @click="sendQuery">Add Location</button>
      </div>
      <div 
        v-else
        class="byCoordinate"
      >
        <input type="text" placeholder="latitude" v-model="latitude" />
        <input type="text" placeholder="longitude" v-model="longitude" />
        <button @click="sendLocation">Add Location</button>
      </div>
    </div>
    <div class="form_switch">
      <button @click="changeVisual">
        Search by {{ !this.placeByName ? "Name" : "GPS Coordinates" }}
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "FormComp",
  data() {
    return {
      placeByName: true,
      longitude: "",
      latitude: "",
      query: "",
    };
  },
  methods: {
    sendLocation() {
      this.$emit("send-location", this.longitude, this.latitude);
      this.longitude = "";
      this.latitude = "";
    },
    sendQuery() {
      this.$emit("send-query", this.query);
      this.query = "";
    },
    changeVisual() {
      this.placeByName = !this.placeByName;
    },
  },
};
</script>

<style lang="scss">
#form {
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .form_search {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0;
    
    // background: red;
    @media all and (max-width: 450px) {
      flex-direction: column;
      padding: 10px;
    }

    input {
      padding: 0.5rem;
      // margin-right: 0.5rem;
      font-size: 1rem;
      border-radius: 5px;
      border: 1px dotted black;
    }

    button {
      font-size: 1rem;
      padding: 0.5rem;
      background-color: rgb(95, 158, 160);
      border: none;
      border-radius: 5px;
      color: white;
      margin-left: 3px;
    }

    input,
    button {
      // margin-bottom: 5px;

      @media all and (max-width: 1100px) {
        padding: 0.5rem;
      }

      @media all and (max-width: 900px) {
        // font-size: 0.8rem;
      }

      @media all and (max-width: 600px) {
        // font-size: 0.7rem;
      }

      @media all and (max-width: 350px) {
          font-size: 0.7rem;
      }
    }

    .byName,
    .byCoordinate {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .byCoordinate {
      @media all and (max-width: 600px) {
        flex-direction: column;
        input {
          margin-bottom: 3px;
          width: 80vw;
        }
      }
    }
  }
  .form_switch {
    button {
      font-size: 1rem;
      padding: 0.7rem;
      background-color: rgb(160, 95, 149);
      border: none;
      border-radius: 5px;
      color: white;
      margin-bottom: 10px;
      @media all and (max-width: 1100px) {
        padding: 0.5rem;
      }

      @media all and (max-width: 900px) {
        // font-size: 0.8rem;
      }

      @media all and (max-width: 600px) {
        // font-size: 0.7rem;
      }

      @media all and (max-width: 350px) {
          font-size: 0.7rem;
      }
    }
  }
}
</style>
