<template>
  <div id="location">
    <Form @send-location="useLocation" @send-query="useQuery" />
    <!-- <div ref="mapRef" class="map"></div> -->
    <div id="dashboard">
      <Tabs
        :tab-style="{
          backgroundColor: 'transparent',
        }"
      >
        <Tab title="Map">
          <div class="tomtom container" id="tomtom">
            <div class="row text-center">
              <div class="col-sm-12 col-lg-4">
                <div id="info" class="info">
                  <img
                    class=""
                    src="@/assets/img/geocoder/geocoder.png"
                    alt="geocoder"
                  />
                </div>
              </div>
              <div class="col-sm-12 col-lg-8">
                <div id="map" class="map">
                  <div class="coordinates" id="coordinates"></div>
                </div>
              </div>
            </div>
          </div>
        </Tab>

        <Tab title="Forecast">
          <div class="bg" v-if="Object.keys(weather).length === 0">
            <img
              src="../../assets/img/geocoder/geocoder.png"
              alt="forecast geocoder"
            />
          </div>
          <div v-if="search" class="open_meteo container" id="open_meteo">
            <div class="row mb-3 top">
              <div class="col-12">
                <h2>Forecast Weather</h2>
                <h4>
                  <a href="https://open-meteo.com/" target="blank"
                    >by Open-Meteo</a
                  >
                </h4>
              </div>
            </div>
            <div class="row mb-3 forecast" ref="forecast">
              <div class="col-12">
                <div class="row weather">
                  <div class="left col-sm-12 col-lg-7 tile">
                    <span class="w_key title"
                      >{{
                        getDayName(weather.data.daily.time[this.id], "en-EN")
                      }}
                      Highlights</span
                    >
                    <div class="highlights">
                      <div class="today">
                        <h5>Sunrise & Sunset</h5>
                        <div class="info">
                          <div class="sunrise">
                            <img
                              src="@/assets/img/geocoder/svg/sun_sunrise.svg"
                              alt=""
                            />
                            <p class="">
                              {{
                                getHoursAndMinutes(
                                  weather.data.daily.sunrise[this.id]
                                )
                              }}
                            </p>
                          </div>
                          <div class="sunset">
                            <img
                              src="@/assets/img/geocoder/svg/sun_sunset.svg"
                              alt=""
                            />
                            <p class="">
                              {{
                                getHoursAndMinutes(
                                  weather.data.daily.sunset[this.id]
                                )
                              }}
                            </p>
                          </div>
                        </div>
                      </div>
                      <div class="today">
                        <h5>Min & Max</h5>
                        <div class="info">
                          <div class="min">
                            <img
                              src="@/assets/img/geocoder/svg/min.svg"
                              alt="temp min"
                            />
                            <p class="position-relative">
                              {{
                                weather.data.daily.temperature_2m_min[this.id]
                              }}<span class="celsius">C</span>
                            </p>
                          </div>
                          <div class="max">
                            <img
                              src="@/assets/img/geocoder/svg/max.svg"
                              alt="temp max"
                            />
                            <p class="position-relative">
                              {{
                                weather.data.daily.temperature_2m_max[this.id]
                              }}<span class="celsius">C</span>
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="highlights">
                      <div class="today">
                        <h5>Precipitation Probability</h5>
                        <div class="info">
                          <div class="rain_prob">
                            <img
                              src="@/assets/img/geocoder/svg/percentage_precipitation.svg"
                              alt=""
                            />
                            <p class="position-relative">
                              {{
                                weather.data.daily
                                  .precipitation_probability_max[this.id]
                              }}<span class="celsius">%</span>
                            </p>
                          </div>
                        </div>
                      </div>
                      <div class="today">
                        <h5>Wind Direction</h5>
                        <div class="info">
                          <div class="wind">
                            <img
                              src="@/assets/img/geocoder/svg/wind.svg"
                              alt=""
                            />
                            <p class="position-relative">
                              {{
                                weather.data.daily.winddirection_10m_dominant[
                                  this.id
                                ]
                              }}<span class="celsius"></span>
                            </p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="right col-sm-12 col-lg-4 offset-lg-1">
                    <div class="day">
                      <h2>
                        {{
                          getDayName(weather.data.daily.time[this.id], "en-EN")
                        }}
                      </h2>
                      <div class="tile">
                        <div class="address" id="address">
                          <h3>
                            {{
                              this.location.data.addresses[0].address
                                .municipality
                            }}
                          </h3>
                          <h5>
                            {{
                              this.location.data.addresses[0].address
                                .countrySecondarySubdivision
                            }},
                            {{
                              this.location.data.addresses[0].address
                                .countrySubdivision
                            }},
                            {{
                              this.location.data.addresses[0].address
                                .countryCode
                            }}
                          </h5>
                        </div>
                        <hr />
                        <div class="now" v-if="this.id == 0">
                          <span class="w_key"
                            >Now: {{ getCurrentTime() }}
                          </span>
                          <div class="now_weather">
                            <span class="w_key temp">
                              {{
                                this.weather.data.current_weather.temperature
                              }}
                              <span class="celsius">C</span>
                            </span>
                            <div
                              v-if="
                                this.weather.data.current_weather.is_day == 1
                              "
                            >
                              <!-- day -->
                              <img
                                :src="
                                  require(`@/assets/img/geocoder/svg/${getWeatherIconDay(
                                    this.weather.data.current_weather
                                      .weathercode
                                  )}.svg`)
                                "
                                :alt="
                                  getWeatherIconDay(
                                    this.weather.data.current_weather
                                      .weathercode
                                  )
                                "
                              />
                            </div>
                            <!-- night -->
                            <div v-else>
                              <img
                                :src="
                                  require(`@/assets/img/geocoder/svg/${getWeatherIconNight(
                                    this.weather.data.current_weather
                                      .weathercode
                                  )}.svg`)
                                "
                                :alt="
                                  getWeatherIconNight(
                                    this.weather.data.current_weather
                                      .weathercode
                                  )
                                "
                              />
                            </div>
                          </div>
                        </div>
                        <div v-else>
                          <div>
                            Today's weather? click 
                            <span
                            @click="getMeteo(0)" 
                              style="color:gold; cursor:pointer"
                            >
                            here
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row mb-5 forecast">
              <div class="col-12">
                <div class="row weather">
                  <div class="center col tile">
                    <span class="w_key title"
                      >{{
                        getDayName(weather.data.daily.time[this.id], "en-EN")
                      }}
                      at</span
                    >
                    <div
                      class="highlights d-flex justify-content-center align-items-center flex-wrap"
                    >
                      <div
                        class="hourly"
                        v-for="(hour, index) in this.day"
                        :key="hour"
                      >
                        <div class="hour">
                          <h5 v-if="index >= 0 && index < 10" class="m-0">
                            {{ "0" + index + ":00" }}
                          </h5>
                          <h5 v-else class="m-0">{{ index + ":00" }}</h5>
                          <div class="hour_time">
                            <!-- day -->
                            <div v-if="hour.isDay == 1">
                              <img
                                :src="
                                  require(`@/assets/img/geocoder/svg/${getWeatherIconDay(
                                    hour.weathercode
                                  )}.svg`)
                                "
                                :alt="getWeatherIconDay(hour.weathercode)"
                              />
                            </div>
                            <!-- night -->
                            <div v-else>
                              <img
                                :src="
                                  require(`@/assets/img/geocoder/svg/${getWeatherIconNight(
                                    hour.weathercode
                                  )}.svg`)
                                "
                                :alt="getWeatherIconNight(hour.weathercode)"
                              />
                            </div>
                            <p class="m-0">{{ hour.temperature }}c</p>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row mb-5 forecast">
              <div class="col-12">
                <div class="row weather">
                  <div class="center col tile">
                    <span class="w_key title">Next Days</span>
                    <div
                      class="highlights d-flex justify-content-center align-items-center flex-wrap"
                    >
                      <div
                        class="hourly h-auto w_days"
                        v-for="day in filteredNextDays"
                        :key="day.id"
                        @click="getMeteo(day.id)"
                      >
                        <div class="hour">
                          <h5 class="d_name">
                            {{ getDayName(day.time, "en-EN") }}
                          </h5>
                          <div class="hour_time">
                            <img
                              :src="
                                require(`@/assets/img/geocoder/svg/${getWeatherIconDay(
                                  day.weathercode
                                )}.svg`)
                              "
                              :alt="getWeatherIconDay(day.weathercode)"
                            />
                          </div>
                          <div class="min_max">
                            <div>
                              <i
                                class="fa-solid fa-caret-down"
                                style="color: #0158ef"
                              ></i>
                              <span> {{ day.temperature_min }}</span>
                            </div>
                            <div>
                              <i
                                class="fa-solid fa-caret-up"
                                style="color: #c91818"
                              ></i>
                              <span> {{ day.temperature_max }}</span>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Tab>
      </Tabs>
    </div>
  </div>
</template>

<script>
import tt from "@tomtom-international/web-sdk-maps";
import axios from "axios";
import Form from "@/components/Geocoder/Form.vue";
import Tabs from "@/components/Tabs/Tabs.vue";
import Tab from "@/components/Tabs/Tab.vue";

export default {
  name: "MapComp",
  components: {
    Form,
    Tabs,
    Tab,
  },
  data() {
    return {
      welcomeGeocoder: false,
      detailsAreVisible: false,
      search: false,
      // 12.5113300
      // 41.8919300
      query: "",
      long: null,
      lat: null,
      key: "TYMEGvpqbUgpY10zZu3mHGsSeeLdlngD",
      location: {},
      weather: {},
      hourly: {},
      day: [],
      nextDays: {},
      id: null,
    };
  },
  computed: {
    filteredNextDays() {
      return this.nextDays.filter((item) => item.id != 0);
    },
  },
  mounted() {
    this.defaultMap();
  },
  methods: {
    defaultMap() {
      var map = tt.map({
        key: this.key,
        container: "map",
      });
      map.addControl(new tt.FullscreenControl());
      map.addControl(new tt.NavigationControl());
    },
    useLocation(long, lat) {
      this.long = long;
      this.lat = lat;
      this.getMap();
    },
    useQuery(query) {
      this.query = query;
      this.readQuery();
    },
    readQuery() {
      axios
        .get(
          `https://api.tomtom.com/search/2/poiSearch/${this.query}.json?key=${this.key}`
        )
        .then((result) => {
          // console.log(result);
          this.lat = result.data.results[0].position.lat;
          this.long = result.data.results[0].position.lon;
          this.getMap();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getMap() {
      var map = tt.map({
        key: this.key,
        container: "map",
        center: [this.long, this.lat],
        zoom: 13,
      });
      map.addControl(new tt.FullscreenControl());
      map.addControl(new tt.NavigationControl());
      this.addMarker(map);
    },
    addMarker(map) {
      var location = [this.long, this.lat];
      var popupOffsets = 25;
      var marker = new tt.Marker().setLngLat(location).addTo(map);
      var popup = new tt.Popup({ offset: popupOffsets });
      this.reverseGeocoding(marker, popup);
      marker.setPopup(popup).togglePopup();
      this.getMeteo(0);
    },
    reverseGeocoding(marker, popup) {
      axios
        .get(
          `https://api.tomtom.com/search/2/reverseGeocode/${this.lat},${this.long}.json?key=${this.key}&radius=100`
        )
        .then((result) => {
          // console.log(result);
          this.location = result;
          popup.setHTML(result.data.addresses[0].address.freeformAddress);

          // Aggiunge in un div l'indirizzo
          // var address = document.getElementById("address");
          // address.innerHTML = `<h3>${result.data.addresses[0].address.municipality}</h3>
          //  <h5>${result.data.addresses[0].address.countrySecondarySubdivision}, ${result.data.addresses[0].address.countrySubdivision}, ${result.data.addresses[0].address.countryCode}</h5>`;
          // result.data.addresses[0].address.freeformAddress +
          // ", " +
          // result.data.addresses[0].address.country;
          var coordinates = document.getElementById("coordinates");
          coordinates.innerHTML = `<i class='fa-solid fa-map-pin' style='color: #000000;''> <span> Longitude: ${this.long} -  Latitude: ${this.lat}</span>`;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getMeteo(id) {
      this.search = true;
      this.weather = {};
      // this.days = [];
      if (id == null || id == undefined) {
        id = 0;
      }
      this.id = id;
      // console.log(this.id);
      axios
        .get(
          `https://api.open-meteo.com/v1/forecast?latitude=${this.lat}&longitude=${this.long}&hourly=temperature_2m,relativehumidity_2m,precipitation_probability,precipitation,rain,weathercode,uv_index,uv_index_clear_sky,is_day,cape,freezinglevel_height&daily=weathercode,temperature_2m_max,temperature_2m_min,apparent_temperature_max,apparent_temperature_min,sunrise,sunset,uv_index_max,uv_index_clear_sky_max,precipitation_sum,rain_sum,showers_sum,snowfall_sum,precipitation_hours,precipitation_probability_max,windspeed_10m_max,windgusts_10m_max,winddirection_10m_dominant,shortwave_radiation_sum&timezone=auto&current_weather=true`
        )
        .then((result) => {
          this.weather = result;
          // console.log(this.weather);

          // hourly obj
          const idHour = Array.from(Array(168).keys());
          const isDay = result.data.hourly.is_day;
          const temp = result.data.hourly.temperature_2m;
          const codeHour = result.data.hourly.weathercode;
          this.hourly = this.createObjHourlyFromArrays(
            idHour,
            isDay,
            temp,
            codeHour
          );
          // day array
          this.day = this.splitDailyArray(this.hourly)[this.id];
          // console.log(this.day);

          // nextDays obj
          const idDay = Array.from(Array(7).keys());
          const tempMax = result.data.daily.temperature_2m_max;
          const tempMin = result.data.daily.temperature_2m_min;
          const time = result.data.daily.time;
          const codeDay = result.data.daily.weathercode;
          this.nextDays = this.createObjDailyFromArrays(
            idDay,
            tempMax,
            tempMin,
            time,
            codeDay
          );
          // console.log(this.nextDays);
          this.scrollMeTo("forecast");
        })
        .catch((err) => {
          console.log(err);
        });
    },
    toggleDetails(id) {
      // console.log(id);
      var element = document.getElementById(id);
      element.classList.toggle("visible");
      element.classList.toggle("not_visible");
    },
    getDayName(dateStr, locale) {
      var date = new Date(dateStr);
      return date.toLocaleDateString(locale, { weekday: "long" });
      // var dateStr = '2023-06-12';
      // var day = getDayName(dateStr, "en-EN");
      // console.log(day);
    },
    getHoursAndMinutes(date) {
      var d = new Date(date);
      return d.getHours() + ":" + d.getMinutes();
    },
    getActualTemp() {
      var d = new Date();
      var dateIndex = d.getHours();
      return this.weather.data.hourly.temperature_2m[dateIndex];
    },
    getTemp(index) {
      return this.weather.data.hourly.temperature_2m[index];
    },
    getCurrentTime() {
      var date = new Date(this.weather.data.current_weather.time);
      return date.toLocaleString();
    },
    splitDailyArray(array) {
      const nums = array;
      // Get number of subarrays
      const numsPerGroup = Math.ceil(nums.length / 7);
      // Create n subarrays based on number of item
      const result = new Array(7)
        // Make sure each element isn't empty
        .fill("")
        // For each group, grab the right `slice` of the input array
        .map((_, i) => nums.slice(i * numsPerGroup, (i + 1) * numsPerGroup));
      return result;
    },
    createObjHourlyFromArrays(array1, array2, array3, array4) {
      const arrayOfObjects = array1.map((currentValue, index) => {
        return {
          id: currentValue,
          temperature: array3[index],
          weathercode: array4[index],
          isDay: array2[index],
        };
      });
      return arrayOfObjects;
    },
    createObjDailyFromArrays(array1, array2, array3, array4, array5) {
      // idDay,tempMax, tempMin, time, codeDay
      const arrayOfObjects = array1.map((currentValue, index) => {
        return {
          id: currentValue,
          temperature_max: array2[index],
          temperature_min: array3[index],
          time: array4[index],
          weathercode: array5[index],
        };
      });
      return arrayOfObjects;
    },
    getWeatherIconDay(n) {
      var result = "";
      switch (true) {
        case n == 0:
          result = "sun";
          break;
        case n > 0 && n < 4:
          result = "cloudy_sunny";
          break;
        // case n >= 3 && n < 4:
        //   result = "cloudy_rain";
        //   break;
        case n >= 4 && n < 49:
          result = "fog";
          break;
        case n >= 49 && n < 56:
          result = "drizzle_rain";
          break;
        case n >= 56 && n < 58:
          result = "freezing_drizzle";
          break;
        case n >= 58 && n < 66:
          result = "rain";
          break;
        case n >= 66 && n < 68:
          result = "freezing_rain";
          break;
        case n >= 68 && n < 76:
          result = "snow";
          break;
        case n >= 76 && n < 78:
          result = "snow_grains";
          break;
        case n >= 78 && n < 83:
          result = "heavy_rain";
          break;
        case n >= 83 && n < 87:
          result = "snow_showers";
          break;
        case n >= 87 && n < 96:
          result = "thunder";
          break;
        case n >= 96 && n < 99:
          result = "hail_cloud";
          break;
        default:
          result = "sun";
      }
      return result;
    },
    getWeatherIconNight(n) {
      var result = "";
      switch (true) {
        case n == 0:
          result = "moon_1";
          break;
        case n > 0 && n < 4:
          result = "cloudy_moon";
          break;
        // case n >= 3 && n < 4:
        //   result = "moon_overcast";
        //   break;
        case n >= 4 && n < 49:
          result = "moon_fog";
          break;
        case n >= 49 && n < 56:
          result = "drizzle_rain";
          break;
        case n >= 56 && n < 58:
          result = "freezing_drizzle";
          break;
        case n >= 58 && n < 66:
          result = "moon_rain";
          break;
        case n >= 66 && n < 68:
          result = "freezing_rain";
          break;
        case n >= 68 && n < 76:
          result = "moon_snow";
          break;
        case n >= 76 && n < 78:
          result = "snow_grains";
          break;
        case n >= 78 && n < 83:
          result = "moon_rain";
          break;
        case n >= 83 && n < 87:
          result = "moon_storm";
          break;
        case n >= 87 && n < 96:
          result = "thunder";
          break;
        case n >= 96 && n < 99:
          result = "hail_cloud";
          break;
        default:
          result = "moon_1";
      }
      return result;
    },
    scrollMeTo(refName) {
      var element = this.$refs[refName];
      var top = element.offsetTop;

      window.scrollTo(0, top);
    },
  },
};
</script>

<style lang="scss">
#location {
  min-height: 100vh;
  background: #0b0a0c;
  font-family: "Work Sans", sans-serif;
  padding-bottom: 50px;

  @media all and (max-width: 990px) {
  }

  #dashboard {
    display: flex;
    flex-direction: column;
    padding: 0 20px;

    @media all and (max-width: 992px) {
      flex-direction: column;
    }

    @media all and (max-width: 500px) {
      padding: 0;
    }

    #tomtom {
      //flex-basis: 58vw;
      margin-bottom: 20px;

      #info {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 50vh;
        width: 100%;
        border: 1px dotted #e4dee927;
        border-radius: 10px;
        background: #ffffff;

        @media all and (max-width: 992px) {
          min-height: auto;
          width: auto;
          height: 200px;
          margin-bottom: 10px;
        }

        @media all and (max-width: 500px) {
          display: none;
        }

        img {
          width: 100%;
          height: auto;

          @media all and (max-width: 992px) {
            width: auto;
            height: 100%;
          }
        }
      }

      #map {
        position: relative;
        min-height: 50vh;
        width: 100%;
        color: #0b0a0c;
        border: 1px dotted #e4dee927;
        border-radius: 10px;
        box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px,
          rgba(0, 0, 0, 0.23) 0px 16px 6px;

        @media all and (max-width: 992px) {
          margin-bottom: 10px;
        }

        .coordinates {
          position: absolute;
          width: 100%;
          bottom: 0;
          left: 0;
          z-index: 998;
          color: #000;
          font-weight: bold;
          background: #c2c5beb7;
          text-align: left;
          padding-left: 5px;
        }
      }
    }

    .bg {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        background-color: #5f9ea0;
        border: 1px dotted rgba(228, 222, 233, 0.1529411765);
        border-radius: 10px;
      }
    }

    #open_meteo {
      flex-basis: 40vw;
      // margin-left: 2vw;
      margin-bottom: 20px;
      color: #ffffffc0;

      @media all and (max-width: 992px) {
        margin: 0 auto;
      }

      .top {
        @media all and (max-width: 992px) {
          margin-bottom: -60px;
        }
      }

      .weather {
        display: flex;
        justify-content: center;
        align-items: center;

        @media all and (max-width: 992px) {
          flex-direction: column-reverse;
        }
      }

      a {
