  .visible {
    display: block;
  }

  .not_visible {
    display: none;
  }

  .tile {
    border: 1px dotted #e4dee927;
    border-radius: 10px;
    background: #e4dee909;
    padding: 10px;
  }

  .w_key {
    display: block;
  }

  .bounce-enter-active {
    animation: bounce-in 0.5s;
  }

  .bounce-leave-active {
    animation: bounce-in 0.5s reverse;
  }

  @keyframes bounce-in {
    0% {
      transform: scale(0);
    }

    50% {
      transform: scale(1.25);
    }

    100% {
      transform: scale(1);
    }
  }
