<template>
  <div id="geocoder">
    <!--Navbar Here -->
    <div>
      <nav>
        <div class="header">
          <img src="@/assets/img/geocoder/geocoder.png" alt="geocoder" />
        </div>
      </nav>
      <ConnectionStatus
        :database="database"
        :link-db="link.href"
        :status="status"
        :child-class="status === 'OK' ? 'green' : ''"
        class="black"
      />
    </div>

    <Map />
  </div>
</template>
<script>
import Map from "@/components/Geocoder/Map.vue";
import ConnectionStatus from "@/components/SharedComponent/ConnectionStatus.vue";
import axios from "axios";

export default {
  name: "GeocoderView",
  components: {
    Map,
    ConnectionStatus,
  },
  data() {
    return {
      database: "TomTom",
      link: {
        name: "TomTom",
        href: "https://developer.tomtom.com/search-api/documentation/product-information/introduction",
      },
      status: null,
      key: "TYMEGvpqbUgpY10zZu3mHGsSeeLdlngD",
    };
  },
  methods: {
    isConnected() {
      this.status = "loading";
      axios
        .get(
          `https://api.tomtom.com/search/2/search/123%20main%20st.json?key=${this.key}`
        )
        .then((result) => {
          console.log(result);
          this.status = result.status === 200 ? "OK" : "";
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    this.isConnected();
  },
  mounted() {
    window.scrollTo(0, 0);
  },
};
</script>
<style lang="scss">
#geocoder {
  background: #0b0a0c;
  nav {
    height: 65px;
    background-color: rgb(95, 158, 160);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @media all and (max-width: 992px) {
      justify-content: center;
    }
    .header {
      img {
        width: 150px;
        overflow: hidden;
      }
    }
  }
  .black {
    background: #1d1b1f;
  }
}
</style>
